/* 0. Variables ================================= */
@import 'reset';

$bg-color: #edb1c4;
$mobile-bp: 320px;
$tablet-bp: 768px;
$desktop-bp: 1280px;


@mixin mobile  {
	@media (min-width: $tablet-bp) {
		@content;
	}
}


@mixin tablet  {
	@media (min-width: $tablet-bp) {
		@content;
	}
}

@mixin desktop  {
	@media (min-width: $desktop-bp) {
		@content;
	}
}


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: $bg-color;
}




/* 2. Content ================================= */

header {
	background: #fff;
	width: 100%;
	min-height: 20vh;
	max-height: 15vh;
	position: relative;


	.hamburger {
		position: relative;

		.menu-lines {
			max-width: 6%;
			position: absolute;
			top: 35px;
			left: 7%;
			z-index: 1;

		}

		.pink-menu-bg {
			max-width: 20%;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	

	.shop-icon {
		position: absolute;
		top: 45.75px;
		left: 90%;
		
		border: 1px solid red;
	}


	.header-logo {
		min-width: 12%;
		max-width: 18%;
		position: absolute;
		top: 45.75%;
		left: 50%;
		transform: translate(-50%, -50%);

	}
}

.content {
	background: url('../img/BACKGROUND PATTERN.png');
	background-size: 100vw;
	position: relative;


	.hero-text {
		font-size: 60px;
		text-align: center;
		padding: 20px;
		margin-top: 5px;
		font-weight: 1000;
	}

	.images-hero img {
		width: 20%;
		margin: 5px 20px;
		position: relative;
	}

	.eggs-text {
		position: absolute;
		top: 0;
		left: 0;
		width: 15%;
	}

	.images-hero {
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;

	}


		
	.cashews-text {
		position: absolute;
		top: 0;
		left: 0;
		width: 15%;
	}

	

	.dates-text {
		position: absolute;
		top: 0;
		left: 0;
		width: 15%;
	}


	
	.shop-now-btn-home {
		text-decoration: none;
		padding: 15px;
		background: white;
		border: 1.5px solid black;
		font-size: 20px;
		text-align: center;
		color: black;
		display: block;
		margin-left: auto;
		margin-right: auto;
		width: 35%;
		margin-top: 40px;
	}

	.shop-now-btn-home:hover {
		background: #edb1c4;
	}
}